import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../auth/auth.service';
import { Router } from '@angular/router';
import { ConfigService } from '../../../config/config.service';

@Component({
  selector: 'app-hello-user-button',
  templateUrl: './hello-user-button.component.html',
  styleUrls: ['./hello-user-button.component.scss']
})
export class HelloUserButtonComponent implements OnInit {
  constructor(private authService: AuthService, private router: Router, private config: ConfigService) { }

  public logout() {
    this.authService.logout().subscribe(() => {
      this.router.navigateByUrl(this.config.getPanelUrl('login')).then(() => {});
    });
  }

  ngOnInit() {
  }

}
