import { Component, OnDestroy, OnInit } from '@angular/core';
import { AuthService } from '../../auth/auth.service';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import {
  ActivatedRoute,
  Event,
  NavigationCancel,
  NavigationEnd,
  NavigationError,
  NavigationStart,
  Router
} from '@angular/router';
import { RouteHelper } from '../../ui-api/route-helper';
import { Subscription } from 'rxjs';
import { delay } from 'rxjs/operators';
import { VERSION } from '../../../../environments/version';

@Component({
  selector: 'app-panel-layout',
  templateUrl: './panel-layout.component.html',
  styleUrls: ['./panel-layout.component.scss'],
  animations: [
    trigger('openCloseSidebar', [
      state('closed', style({
        left: '-237px',
      })),
      transition('open => closed', [
        animate('430ms ease-in-out')
      ]),
      transition('closed => open', [
        animate('430ms ease-in-out')
      ]),
    ]),
    trigger('alignContent', [
      state('closed', style({
        paddingLeft: '0px',
      })),
      transition('open => closed', [
        animate('430ms ease-in-out')
      ]),
      transition('closed => open', [
        animate('430ms ease-in-out')
      ]),
    ]),
    trigger('openCloseTopBar', [
      state('closed', style({
        marginTop: '-67px',
      })),
      transition('open => closed', [
        animate('430ms ease-in-out')
      ]),
      transition('closed => open', [
        animate('430ms ease-in-out')
      ]),
    ]),
  ],
})
export class PanelLayoutComponent implements OnInit, OnDestroy {
  showTopBar = true;
  showSidebar: boolean;
  loading = false;
  private readonly routeHelper: RouteHelper;
  private routerEventsSubscription: Subscription;
  private isLoggedInSubscription: Subscription;
  private innerWidth: any;
  private version = VERSION.version;

  constructor(private auth: AuthService, private router: Router, private activatedRoute: ActivatedRoute) {
    this.routeHelper = new RouteHelper(activatedRoute);
  }

  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.isLoggedInSubscription = this.auth.isLoggedIn.subscribe(
      isLoggedIn => {
        this.innerWidth = window.innerWidth;
        if (this.innerWidth > 1280) {
          // show sidebar on web page enter only for large screens
          this.showSidebar = isLoggedIn;
        } else {
          this.showSidebar = false;
        }
        this.showTopBar = isLoggedIn;
      }
    );

    this.routerEventsSubscription = this.router.events.pipe(delay(0)).subscribe((event: Event) => {
      switch (true) {
        case event instanceof NavigationStart: {
          const urlChanges = this.routeHelper.getUrlChanges((event as NavigationStart).url);
          this.loading = urlChanges.path || urlChanges.query;
          break;
        }

        case event instanceof NavigationEnd:
        case event instanceof NavigationCancel:
        case event instanceof NavigationError: {
          this.loading = false;
          break;
        }
        default: {
          break;
        }
      }
    });
  }

  ngOnDestroy(): void {
    this.isLoggedInSubscription.unsubscribe();
    this.routerEventsSubscription.unsubscribe();
  }
}
