import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { ConfigService } from '../../config/config.service';
import { IAuthResultModel } from '../models/auth-result.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm = this.formBuilder.group({
    username: ['', Validators.required],
    password: ['', Validators.required],
    rememberMe: [false]
  });

  @ViewChild('formErrors', {static: false}) formErrors;

  constructor(
    private formBuilder: FormBuilder, private router: Router,
    private auth: AuthService, private config: ConfigService,
  ) { }

  ngOnInit() {
  }

  login() {
    this.auth.login(
      this.loginForm.controls.username.value,
      this.loginForm.controls.password.value,
      this.loginForm.controls.rememberMe.value,
    ).subscribe(
      (authResult: IAuthResultModel) => {
       if (authResult.success) {
         // TODO: see about this warning
         // TODO: go back instead of home if possible
         this.router.navigateByUrl(
           this.config.getPrevUrl()
         ).then(() => {});
       } else {
         this.formErrors.showError(authResult.errorMessage);
       }
      }
    );
  }
}
